<template>
    <div>
        <b-pagination
            id = 'pagination'
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="ticketTable"
        ></b-pagination>

        <b-table 
        id="ticketTable"
        striped hover 
        :items="tickets" 
        :fields="fields"
        :sort-by.sync="sortBy"
        :per-page="perPage"
        :current-page="currentPage"
        :busy="isBusy"
        >
            
    <template v-slot:cell(assigned)="ticketitem">
        <b-dropdown id="dropdown-assign" :text="ticketitem.item.assigned ? ticketitem.item.assignee: 'unassigned'" class="m-md-2">
            <escortDropdown 
            class = 'mx-auto'
            v-bind:escorts='escorts' 
            v-bind:ticketitem='ticketitem'
            v-on:assign="$emit('assign', {$event, ticketId: ticketitem.item._id})"
            />
        </b-dropdown>
      </template>

    <template v-slot:cell(action)="ticketitem">
            <b-button id = 'resolve' @click="$emit('resolve', ticketitem.item._id)">
            resolve
            </b-button>
    </template>

      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
          <strong>Loading...</strong>
        </div>
      </template>


    </b-table>



    </div>
</template>

<script>
import escortDropdown from './escortDropdown.vue'


export default {
    name: "tickets",
    components: {
        escortDropdown
    },
    data() {
        return {
            sortBy: 'date',
            currentPage: 1,
            perPage: 6
        }
    },
    props: ["tickets", "escorts", "fields", "isBusy"],
    computed: {
      rows() {
        return this.tickets.length
      }
    }
}
</script>

<style scoped>
.pagination {
  padding-top: 13px;
  justify-content: center;
}

</style>