<template>
  <div id="app">
    <h1>FL escort request ticket tracker</h1>
    <b-button
    v-on:click="changeResolve"
    >show {{ resolved ? 'resolved': 'unresolved' }} tickets</b-button>
    <tickets 
    v-bind:tickets="tickets" 
    v-bind:escorts="escorts"
    v-bind:fields="fields"
    v-bind:isBusy="isBusy"
    v-on:resolve="resolve" 
    v-on:assign="assign"
    />

  </div>
</template>

<script>

import tickets from './components/tickets';
import axios from 'axios';

export default {
  name: 'App',
  components: {
    tickets
  },
  data() {
    return {
      escorts: [],
      tickets: [],
      resolved: false,
      isBusy: false,
      fields: [],
      fieldsUnresolved: [
        {
          key: 'date',
          sortable: true
        },
        {
          key: 'time',
          sortable: false
        },
        {
          key: 'name',
          label: 'requester',
          sortable: true
        },
        {
          key: 'details',
          class: 'details'
        },

        {
          key: 'assigned',
          label: 'assigned'
        },
        {
          key: 'action',
        }
      ],
      fieldsResolved: [
        {
          key: 'date',
          sortable: true,
          variant: 'success'
        },
        {
          key: 'time',
          sortable: false,
          variant: 'success'
        },
        {
          key: 'name',
          label: 'requester',
          sortable: true,
          variant: 'success'
        },
        {
          key: 'details',
          variant: 'success',
          class: 'details'
        },
        {
          key: 'resolveTime',
          sortable: true,
          label: 'resolved',
          variant: 'success'
        },
        {
          key: 'assignee',
          label: 'assigned',
          variant: 'success'
        }
      ]
    }
  },
  methods: {
    resolve(id) {
      axios.post('/api/resolveTicket', {id: id})
      .then(res => {
        if (res.status == 200) {
          this.tickets = this.tickets.filter(ticket => ticket._id !== id)
        } else {
          console.log('error recieving response from server')
        }
      })
      .catch(err => console.log(err))
    },
    assign(escort) {     
      axios.post('/api/assignTicket', 
      {
        ticketId: escort.ticketId, 
        assignee: escort.$event
      })
      .then(res => {
        if (res.status == 200) {
          var index = this.tickets.findIndex(ticket => ticket._id == escort.ticketId)
          console.log(index)
          this.tickets[index].assigned = true
          this.tickets[index].assignee = escort.$event.name
        }
      })
      console.log(escort)
    },
    ticketGet(resolved) {
    this.busy(true)
      let resolve = resolved ? 'resolved':'unresolved'
        if (this.resolved == true) {
          this.fields = this.fieldsResolved
        } else if (this.resolved == false) {
          this.fields = this.fieldsUnresolved
        }    
    axios.get('/api/tickets/' + resolve)
      .then(res => {
        this.tickets = res.data

        this.busy(false)
      })
      .catch(err => console.log(err));
    },
    changeResolve() {
      this.resolved = this.resolved ? false:true
      this.ticketGet(this.resolved)
    },
    busy(status) {
      if (status == true) {
        this.isBusy = true
      } else if(status == false) {
        this.isBusy = false
      }
    }
  },

  created() {
    axios.get('/api/escorts')
      .then(res => this.escorts = res.data)
      .catch (err => console.log(err));
    this.fields = this.fieldsUnresolved
    this.ticketGet(this.resolved)
  }
}
</script>

